import * as types from "../types";

export const initialState = {
  currentEmail: "",
  customerId: "",
};
export const emailReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SAVE_EMAIL:
      var newState = {
        ...state,
        currentEmail: action.payload.email,
        customerId: action.payload.customerId,
      };

      return newState;
    default:
      return state;
  }
};
