import { useHistory } from "react-router-dom";
import React, { useState, useEffect, useContext } from "react";
import Axios from "axios";
import UserContext from "../../context/UserContext";
//import { useSelector } from "react-redux";
import Moment from "react-moment";


import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import IconButton from '@mui/material/IconButton';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import MoreVertIcon from '@mui/icons-material/MoreVert'
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';

// import * as Actions from "../../redux/action";
import "./index.css";
import logoImg from "../../images/fino.png";

// import { useDispatch } from "react-redux";


const Transition = React.forwardRef(function Transition(props, ref) {
      return <Slide direction="up" ref={ref} {...props} />;
    });

const Item = styled(Paper)(({ theme }) => ({
      position:'relative',
  backgroundColor:'#fcfeffd6',
  padding: theme.spacing(1),
  textAlign: 'left',
  color: theme.palette.text.secondary,
}));


const Img = styled('img')({
      display:'block',
      width:'30px',
      heigth:'30px'
    });

export default function Translist() {

  // const dispatch = useDispatch();
    const history = useHistory();
    const [tranData, setTranData] = useState([]);
    const { loginStatus, setLoginStatus, setConf, setPdf } = useContext(UserContext);
    const [sellerId, setSellerId] = useState([]);
 //   const email = useSelector((state) => state.email.currentEmail);
    const [totalAmount, setTotalAmount] = useState(0);
    const [openDialog, setDialogOpen] = React.useState(false);


    useEffect(() => {
      const checkLoggedIn = async () => {
            let token = localStorage.getItem("auth-token");
            if (token === null) {
              // history.push("/");
              // return;
            }else{
              const userRes = await Axios.get("/users/", {
                headers: { "x-auth-token": token },
              });
              setLoginStatus({
                token,
                email: userRes.data.email,
                customerId:''
              });
              Axios.get("/users/getId/" + userRes.data.email).then(async (response) => {
                  setSellerId(response.data);
                  if(!response.data.name){
                    setDialogOpen(true)
                  }
                });
                  Axios.get("/trans/all/" + userRes.data.id).then(
                    async (response) => {
                        const currentDate = new Date();
                        const curFormatDate = currentDate.toISOString().slice(0, 10);
            
                        var totalAmount = 0;
                        for (var i = 0; i < response.data.length; i++) {
                            const tranFormatDate = new Date(response.data[i].date)
                                .toISOString()
                                .slice(0, 10);
                            if (curFormatDate === tranFormatDate) {
                                totalAmount += response.data[i].amount;
                            }
                        }
                        setTotalAmount(totalAmount);
                        setTranData(response.data);
                    }
                );
              }
      };
      checkLoggedIn();
      return(()=>{
        setTotalAmount(0);
        setTranData([]);
        setSellerId([])
      })
    // eslint-disable-next-line react-hooks/exhaustive-deps
}, []);
   




//Dialog Settings --------------------
    const handleDialogClickOpen = () => {
      setDialogOpen(true);
    };
    const handleDialogClose = () => {
      setDialogOpen(false);
    };
// Dropmenu settings ----------------------------
      const [anchorEl, setAnchorEl] = React.useState(null);
      const open = Boolean(anchorEl);
      const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
      };
      const handleClose = () => {
        setAnchorEl(null);
      };
//----------------------------------------------------
      const goCustomer = () => {
        localStorage.removeItem('customerId')
        history.push("/customer");
      };
         
      const logout = () => {
        setLoginStatus({
          token: undefined,
          email: undefined,
          customerId:''
        });
        localStorage.removeItem("auth-token");
        localStorage.removeItem("customerId");
        history.push("/");
        return;
      };
// Show transaction all ----------------------
        const [showAll , setShowAll] = useState(false);
        const setting = ()=>{
            setShowAll(prev =>{
                return !prev
            })
        }
        const length = tranData.length;

        const click = (amount, id, pdfLink)=>{
          setConf(false);
        //   const payload = {
        //     email: email,
        //     customerId: id,
        // };
        setPdf(pdfLink);
          // dispatch(Actions.saveEmail(payload));
          setLoginStatus({
            ...loginStatus,
            customerId: id
          })
          console.log(id+"dafaw")
          if(id){
              localStorage.setItem("customerId", id);
          }
         
          history.push({
            pathname: "/confirm/" + amount,
        });
        }
  return (
      <Grid
      sx={{
            height: '100vh',
            color:'white'
          }}
      container
      direction="column"
      justifyContent="flex-start"
    >
      <Grid item className="header" container spacing={2} dirction="row" justifyContent="space-between" sx={{backgroundColor:"#1a1938", p:2}}>
        <Grid item xs={2}
  justifyContent="space-between" container>
            <Img src={logoImg} alt="logo" sx={{my:'auto'}}/>
        </Grid>
        <Grid item xs={8}>
            <Grid>
            {sellerId["name"] ? sellerId["name"] : <Button variant="filled" onClick={handleDialogClickOpen}>
                        Please be a link
                  </Button>}
                  
            </Grid>
            <Grid>
              {sellerId["ruc"] ? `RUC: ${sellerId["ruc"]}` : "+51 943204360"}
            </Grid>
        </Grid>
        <Grid item xs={2} justifyContent="flex-end" container>
            <IconButton
                  id="basic-button"
                  aria-controls={open ? 'basic-menu' : undefined}
                  aria-haspopup="true"
                  aria-expanded={open ? 'true' : undefined}
                  onClick={handleClick}
                  sx={{color:'white'}}
            >
                  <MoreVertIcon sx={{color:'white'}}/>
            </IconButton>
            <Menu
                  id="basic-menu"
                  anchorEl={anchorEl}
                  open={open}
                  onClose={handleClose}
                  MenuListProps={{
                  'aria-labelledby': 'basic-button',
                  }}
            >
                  <MenuItem onClick={handleClose}>Profile</MenuItem>
                  <MenuItem onClick={logout}>Logout</MenuItem>
            </Menu>          
        </Grid>
        <Grid item xs={12}>
            <Item >
                  <span>Today</span>
                  <span style={{position:'absolute',right:'10px'}}>S/&nbsp;<span>{totalAmount.toFixed(2)}</span></span>
            </Item >
           
        </Grid>
      </Grid>
      <Grid item className="body" >
      <Paper sx={{ overflow: 'hidden', width:'100%' }}>
      <TableContainer sx={{ maxHeight: 'calc(100vh - 220px)' }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
          <TableRow>
            <TableCell>ltimos Movimientos</TableCell>
            <TableCell align="right">
              <Button  variant="text"
                    color="info" onClick={setting} sx={{fontSize:'13px', p:0}}>Ver Todos</Button>
            </TableCell>
          </TableRow>
          </TableHead>
          <TableBody>
          {tranData.map((row, index) => (
            <TableRow
              key={index}
              onClick={()=>click(row.amount, row.customerId, row.pdfLink)}
              hover
              sx={{ '&:last-child td, &:last-child th': { border: 0 }, p:0 }}
            >
              <TableCell component="th" scope="row" sx={{p:1}}>
                <strong>{row["name"]}</strong>
                <p className="type">
                  {row["type"]}&nbsp;::&nbsp;
                  <Moment format="Do MMMM - HH:mm">
                    {row["date"]}
                  </Moment>
                </p>
              </TableCell>
              <TableCell align="right" >S/&nbsp;&nbsp;{row.amount}</TableCell>
              
            </TableRow>
          )).reverse().slice(0, (showAll === true ? length : 8)) 
          }
          </TableBody>
        </Table>
      </TableContainer>
      
    </Paper>
      </Grid>
      <Grid item className="footer" sx={{position:'fixed', bottom:'0px',width:'100%'}}>
                  <Button
                    variant="contained"
                    onClick={goCustomer}
                    color="info"
                    sx={{marginBottom:'10px', width:'60%'}}
                    >
                    SEND
                </Button>
      </Grid>

      <Dialog
        open={sellerId["name"]? false : openDialog}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleDialogClose}
        fullWidth
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle sx={{mx:'auto'}} color="primary" >{"Go to the pro version!!"}</DialogTitle>
        <DialogContent>
          <DialogContentText sx={{fontSize:'80%'}} id="alert-dialog-slide-description">
            Welcome to Fino: you are currently in a trial version. If you want to use the pro version, please contact our phone numbers: +51 943204360 or press the button bellow.
          </DialogContentText>
        </DialogContent>
        <DialogActions sx={{mx:'auto'}}>
          <Button sx={{p:1, fontSize:'70%', }} startIcon={<WhatsAppIcon color="white" sx={{fontSize:'80px'}} fontSize="large" />} color="success" variant="contained" onClick={()=>{window.open("https://api.whatsapp.com/send?phone=+51943204360&text=Hola,%20deseo%20soporte%20para%20integrar%20mi%20sistema%20a%20FINO.");
      }}>Contactar por WhatsApp</Button>
        </DialogActions>
      </Dialog>
    </Grid>
  );
}