import React, { useState, useEffect, useContext } from "react";
import Box from "@mui/material/Box";
import UserContext from "../../context/UserContext";
// import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import Axios from "axios";
import InputBase from "@mui/material/InputBase";
import Grid from "@mui/material/Grid";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import { useToasts } from "react-toast-notifications";
// import { useDispatch } from "react-redux";
//
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import Paper from "@mui/material/Paper";
import IconButton from "@mui/material/IconButton";
import SearchIcon from "@mui/icons-material/Search";
import DeleteIcon from '@mui/icons-material/Delete'
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
// import * as Actions from "../../redux/action";

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

import TextField from '@mui/material/TextField';

import {
    SwipeableList,
    SwipeableListItem,
    SwipeAction,
    TrailingActions,
    Type as ListType
  } from "react-swipeable-list";
  import "react-swipeable-list/dist/styles.css";
 
const BootstrapInput = styled(InputBase)(({ theme }) => ({
    "label + &": {
        marginTop: theme.spacing(3),
    },
    "& .MuiInputBase-input": {
        position: "relative",
        backgroundColor: "white",
        fontSize: 14,
        width: "100%",
        padding: "12px 16px",
        transition: theme.transitions.create([
            "border-color",
            "background-color",
            "box-shadow",
            "border-width",
        ]),
        "&:focus": {
            borderColor: "#FA4616",
            borderWidth: "1px",
        },
    },
}));

export default function Customer() {
    // const dispatch = useDispatch();
    const history = useHistory();
    const { loginStatus, setLoginStatus } = useContext(UserContext);
    const { addToast } = useToasts();
    const [allCustomer, setAllCustomer] = useState([]);
    const [customerInfo, setCustomerInfo] = useState({
        sellerId: "",
        name: "",
        doc: "",
        direction: "",
    });
    const [docType, setDocType] = useState({
        invoice: false,
        ticket: false,
        note: false
  
      });

    useEffect(() => {
        const checkLoggedIn = async () => {
            localStorage.removeItem('customerId')
            let token = localStorage.getItem("auth-token");
            if (!token) {
              history.push("/");
              return;
            }
              const userRes = await Axios.get("/users/", {
                headers: { "x-auth-token": token },
              });
              setLoginStatus({
                token,
                email: userRes.data.email,
                customerId:''
              });

            Axios.get("/users/getId/" + userRes.data.email).then(async (response) => {
                setCustomerInfo({
                    ...customerInfo,
                    "sellerId": response.data._id
                });
                setDocType({...docType, 'invoice' : response.data.docType[0], 'ticket' : response.data.docType[1], 'note' : response.data.docType[2]})
            });
            Axios.get("/customers/all/" + userRes.data.id).then(async (res) => {
                setAllCustomer(res.data);
            });
      };
    checkLoggedIn();

    return()=>{
        setCustomerInfo([]);
        setAllCustomer([]);
        setDocType({})
    } // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);

  
    // When creating new customer.
    const saveCustomer = async () => {
        if (!customerInfo["name"]) {
            addToast("Please enter name", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            return;
        }
        if (!customerInfo["doc"]) {
            addToast("Please enter doc number", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            return;
        }
        if (!customerInfo["direction"]) {
            addToast("Please enter direction", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            return;
        }

        const result = await Axios.post("/customers/create", customerInfo);

        // Register Success Case
        if (result.data.status === 200) {
            
            setCustomerInfo({
                ...customerInfo,
                "doc": "",
                "name": "",
                "direction": "",
            });
            // setDoc('');
            // setShow(false);
            Axios.get("/customers/all/" + customerInfo["sellerId"]).then(
                async (response) => {
                    let aaa = response.data.pop();
                    selectCustomer(aaa["id"]);
                }
            );
          //  selectCustomer(aaa.id);
        } else {
            addToast(result.data.msg, {
                appearance: "error",
                autoDismiss: true,
            });
        }
    };

 
    // When seller selects customer.
    const selectCustomer = (id) => {
        // Store current customer's ID to redux
        // const payload = {
        //     email: email,
        //     customerId: id,
        // };

        Axios.get("/customers/get/" + id).then(async (response) => {
        if((!docType.ticket && !docType.note)&&(response.data[0].doc.length === 8)){
            addToast("You can't invoice a DNI!!! Please link to admin.", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            setShow(false);
            setDoc('');
            Axios.get("/customers/all/" + customerInfo["sellerId"]).then(
                async (response) => {
                    setAllCustomer(response.data);
                }
            );
          }else{
            // dispatch(Actions.saveEmail(payload));
            setLoginStatus({
                ...loginStatus,
                customerId: id
              })
              
              localStorage.setItem("customerId", id);
            history.push("/invoice");
          }
      })

}
    // Go to previous page.
    const prevPage = () => {
        history.push('/translist');
    };

    const [doc, setDoc] = useState('');
    const [show, setShow] = useState(false);
    // Search functionality
    const search = (e) => {
        e.preventDefault();
        setDoc(e.target.value);
        // if search work doesn't exist,
        if (e.target.value) {
            const searchKey = e.target.value.toString().toLowerCase();
            let searchResult = [];
            for (var i = 0; i < allCustomer.length; i++) {
                if (
                    allCustomer[i]["name"]
                        .toString()
                        .toLowerCase()
                        .includes(searchKey) ||
                    allCustomer[i]["doc"]
                        .toString()
                        .toLowerCase()
                        .includes(searchKey)
                ) {
                    searchResult.push(allCustomer[i]);
                }
            }
            setAllCustomer(searchResult);
        } else {
            setShow(false);
            Axios.get("/customers/all/" + customerInfo["sellerId"]).then(
                async (response) => {
                    setAllCustomer(response.data);
                }
            );
            setCustomerInfo({
                ...customerInfo,
                "doc": '',
                "name": '',
                "direction": ''
            })        }
    };

    const getCustomerFromAPI=()=>{
        setText('');
        setCustomerInfo({
            ...customerInfo,
            "doc": doc,
            "name": "",
            "direction": "",
        });
        setShow(true);
        if(doc.length !== 8 && doc.length !== 11){
            addToast("Doc number is incorrect", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            setText(`RUC OR DNI, does not exist, type correctly.
            If it is another type of document, type manually.`);
        }
        if(doc.length === 8){
            let url = "https://facturalahoy.com/api/persona/" + doc.toString() +'/facturalaya_evalles_orE26f69ngmqmqB';
            fetch(url)
                .then((res)=> {return res.json()})
                .then((data)=> {
                    if(data.respuesta === "ok"){
                    setCustomerInfo({
                        ...customerInfo,
                        "doc": data.dni,
                        "name": data.nombre,
                        "direction":''
                    })
                }else{
                    addToast("Doc number is incorrect", {
                        appearance: "info",
                        autoDismissTimeout: 2000,
                        autoDismiss: true,
                        placement: 'top-center'
                    });
                    setText(`RUC OR DNI, does not exist, type correctly.
                    If it is another type of document, type manually.`);
                }
                })
                .catch((err)=>{ console.log(err)});
        }
        if(doc.length === 11){
            let url = "https://facturalahoy.com/api/empresa/" + doc.toString() +'/facturalaya_evalles_orE26f69ngmqmqB';
            fetch(url)
                .then((res)=> {return res.json()})
                .then((data)=> {
                    if(data.respuesta === "ok"){
                        setCustomerInfo({
                            ...customerInfo,
                            "doc": data.ruc,
                            "name": data.razon_social,
                            "direction": data.direccion.trim()
                        })
                    }
                    else{
                        addToast("Doc number is incorrect", {
                            appearance: "info",
                            autoDismissTimeout: 2000,
                            autoDismiss: true,
                            placement: 'top-center'
                        });
                        setText(`RUC OR DNI, does not exist, type correctly.
                        If it is another type of document, type manually.`);
                    }
                })
                .catch((err)=>{ console.log(err)});
        }

    }
    // Create Invite Guest.
    const inviteGuest = () => {
        // const payload = {
        //     email: email,
        //     customerId: '',
        // };
        if(!docType.ticket && !docType.note){
            addToast("Now You can't use DNI and Invite", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            // setShow(false);
            // setDoc('');
            // Axios.get("/customers/all/" + customerInfo["sellerId"]).then(
            //     async (response) => {
            //         setAllCustomer(response.data);
            //     }
            // );
          }else{
            // dispatch(Actions.saveEmail(payload));
            localStorage.removeItem('customerId');
            history.push("/invite");
          }
    };
   const [text, setText ] = useState('');
    const handleDelete = (id) => () => {

    Axios.delete("/customers/delete/" + id).then((res) =>{
         if(res.data.msg === "success"){
            addToast("Deleted Successfully", {
                appearance: "success",
                autoDismiss: true,
            });
         }else {
            addToast("Sorry, Operation is failure", {
                appearance: "error",
                autoDismiss: true,
            });
        }
     })
    }
    
  const [open, setOpen] = React.useState(false);
  const [changeData, setChangeData] = useState({
      id:'',
      name: '',
      direction : ''
  });
  const handleEdit = (id, name, direction) => () => {
    setChangeData({
        id: id,
        name :  name,
        direction : direction
    })
    setOpen(true);
  }

  const handleClose = () => {
    setOpen(false);
  };
  const handleChange = async () =>{
    const result = await Axios.put(
        "/customers/update",
        {id: changeData.id, name: changeData.name, direction: changeData.direction}
      );
    if(result.status === 200){
        const newItems = allCustomer.map(item => {
            if (changeData.id === item.id) {
              return { ...item, name: changeData.name, direction : changeData.direction}
            }
            return item;
          });
          setAllCustomer(newItems);
        handleClose();
    }
    else{
        addToast(result.data.msg, {
            appearance: "error",
            autoDismiss: true,
          });
    }
  }
   const trailingActions = (id, name, direction) => (
    <TrailingActions sx={{overflow:'hidden'}}>
      <SwipeAction  sx={{width:'10px'}} onClick={handleEdit(id, name, direction)}>
          
            <Button variant="text" endIcon={<EditIcon  size="large"/>}>
            </Button>
            
      </SwipeAction>
      <SwipeAction destructive={true} onClick={handleDelete(id)}>
       
      <Button variant="text"  startIcon={<DeleteIcon size="large" />}>
            </Button>
      </SwipeAction>
    </TrailingActions>
  );


    return (
            <Box sx={{ height:'100vh', backgroundColor:'#EDEDF5'}}>
                <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px'}}>
                    <ClearIcon />
                </IconButton>
                <p style={{ letterSpacing: '3px', fontWeight:'500',paddingBottom:"20px", fontSize: '20px', color : "#1976d2", paddingTop:'40px'}}>CUSTOMERS</p>

                    <Paper
                        sx={{
                            px:1,
                            mx:1,
                            mb:0,
                            display: "flex",
                            alignItems: "center",
                            marginBottom: "13px",
                            boxShadow: "unset",
                            border: "1px solid #f5f5f5",
                        }}
                    >
                        <InputBase
                            sx={{ ml: 1, flex: 1 }}
                            placeholder="Search Name or Doc in list"
                            inputProps={{ "aria-label": "search google maps" }}
                            onChange={(e) => search(e)}
                            value={doc}
                        />
                        <IconButton onClick={getCustomerFromAPI} sx={{ p: "10px" }} aria-label="search">
                            <SearchIcon  color="primary"/>
                        </IconButton>
                    </Paper>

                    {!show &&<Paper sx={{ overflow: 'hidden',  width:'100%',backgroundColor:'#EDEDF5',mt:"0", boxShadow:'0px 0px 0px 0px' }}>
                        <TableContainer sx={{mx:'auto',width:'95%',Index:'0', maxHeight: 'calc(100vh - 225px)' }}>
                            <Table aria-label="sticky table">
                            <TableBody>
                            <SwipeableList sx={{p:0, m:0}} type={ListType.IOS}>
                            {allCustomer.map((row, index) => (
                                <SwipeableListItem
                                key={index}
                                //leadingActions={leadingActions()}
                                trailingActions={trailingActions(row.id, row.name, row.direction)}
                              >
                                <TableCell 
                                onClick={() => selectCustomer(row.id)} key={index}
                                 scope="row" sx={{p:1,pl:1,mb:1, width:'100%', borderLeft:'3px solid grey'}}>
                                    <strong>{row.name}</strong>
                                    <p >
                                    {row.doc}
                                    </p>
                                </TableCell>
                                </SwipeableListItem>
                            ))
                            }</SwipeableList>
                            </TableBody>
                            </Table>
                        </TableContainer>
                    </Paper>}
                    {show && (<div>
                        <p style={{color:'#1976d2', fontSize:'14px', marginTop:'15px', padding: '0px 50px' }}>{text}</p>
                        <Grid container direction="column" >
                            <BootstrapInput
                                placeholder="Enter Name"
                                name="description"
                                sx={{ width: "85%",mx: 'auto'}}
                                value={customerInfo["name"]}
                                onChange={(e) => {
                                    setCustomerInfo({
                                        ...customerInfo,
                                        "name": e.target.value,
                                    });
                                }}
                                disabled={text === '' ? true : false }
                            />
                        </Grid>
                        <Grid container direction="column" sx={{mt:2}}>
                            <BootstrapInput
                                placeholder="Enter Direction"
                                name="description"
                                sx={{ width: "85%", mx: 'auto'}}
                                value={customerInfo["direction"]}
                                onChange={(e) => {
                                    setCustomerInfo({
                                        ...customerInfo,
                                        "direction": e.target.value,
                                    });
                                }}
                            />
                        </Grid>
                        <Button
                            variant="contained"
                            sx={{ marginTop: "40px" }}
                            onClick={saveCustomer}
                            className="btn-action"
                        >
                            Select
                        </Button>
                    </div>)}
                    <Grid item className="footer" sx={{position:'fixed', bottom:'0px',width:'100%'}}>
                    {!show && <Button  variant="contained"
                    sx={{mb:2, width:'80%'}}
                        onClick={inviteGuest}
                       >
                    INVITADO
                </Button>}
                     </Grid>
                    <Dialog open={open} onClose={handleClose}>
                        <DialogTitle>Edit Customer</DialogTitle>
                        <DialogContent>
                        {/* <DialogContentText>
                            You can change or delete your customer.
                        </DialogContentText> */}
                        <TextField
                            autoFocus
                            focused
                            margin="dense"
                            id="name"
                            label="Name"
                            type="email"
                            fullWidth
                            variant="standard"
                            value= {changeData.name}
                            onChange = {e => setChangeData({...changeData, name : e.target.value})}
                        />
                        <TextField
                            margin="dense"
                            id="direction"
                            focused
                            label="direction"
                            type="text"
                            fullWidth
                            variant="standard"
                            value = {changeData.direction}
                            onChange = {e => setChangeData({...changeData ,direction : e.target.value})}
                        />
                        </DialogContent>
                        <DialogActions>
                        <Button onClick={handleClose}>Cancel</Button>
                        <Button onClick={handleChange}>Change</Button>
                        </DialogActions>
                    </Dialog>
            </Box>
    );
}
