import React, { useState, useContext } from "react";
import { styled } from "@mui/material/styles";
import InputBase from "@mui/material/InputBase";
import Button from "@mui/material/Button";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
import UserContext from "../../context/UserContext";
import Typography from "@mui/material/Typography";
import Axios from "axios";
// import { useDispatch } from "react-redux";
// import * as Actions from "../../redux/action";
import "./index.css";
import IconButton from "@mui/material/IconButton";
import ClearIcon from '@mui/icons-material/Clear';

import Box from '@mui/material/Box';
const BootstrapInput = styled(InputBase)(({ theme }) => ({
    "label + &": {
        marginTop: theme.spacing(3),
    },
    "& .MuiInputBase-input": {
        position: "relative",
        backgroundColor: "white",
        border: "1px solid #D9D9D6",
        borderRadius:'10px',
        width: "100%",
        padding: "10px 15px",
        transition: theme.transitions.create([
            "border-color",
            "background-color",
            "box-shadow",
            "border-width",
        ]),
    },
}));


export default function Login() {
    // const dispatch = useDispatch();
    const { addToast } = useToasts();
    const { setLoginStatus } = useContext(UserContext);
    const history = useHistory();
    const [email, setEmail] = useState("");
    const [values, setValues] = React.useState({
        password: "",
        showPassword: false,
    });

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const login = async () => {
        const password = values.password;

        // Store current user's email to redux
        // const payload = {
        //     email: email,
        //     customerId: "",
        // };
       

        // When user is super admin, redirect to admin page.
        if (email === "superadmin" && password === "superadmin") {
            history.push("/admin");
            return;
        }

        if (!email) {
            addToast("Ingrese su E-mail", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }

        if (!password) {
            addToast("Please Enter Password", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }

        const loginUser = { email, password };
        const loginRes = await Axios.post("/users/login", loginUser);

        if (loginRes.data.status === "No User") {
            addToast(loginRes.data.msg, {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }

        if (loginRes.data.status === "Wrong Password") {
            addToast(loginRes.data.msg, {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }

        if (loginRes.data.status === "No Active") {
            addToast(loginRes.data.msg, {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }

        // If login is success.
        // if (loginRes.data.status === "Success") {
            
            setLoginStatus({
                token: loginRes.data.token,
                email: loginRes.data.email,
                customerId: ''
              });
              localStorage.setItem("auth-token", loginRes.data.token);
            // dispatch(Actions.saveEmail(payload));
            history.push("/translist");
        // }
    };
    const prevPage = () => {
        history.push('/');
    };
    return (
        <Box
          sx={{
            height: '100vh',
            backgroundColor: '#1a1938',
          }}
    >
            <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px'}}>
                <ClearIcon />
            </IconButton>
            <div className="reg-item">
                
            <p className="title">Sign In</p>
                <BootstrapInput
                    placeholder="Email"
                    sx={{ width: "90%", my:'14px', mt:'25px'}}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <BootstrapInput
                    placeholder="Password"
                    sx={{ width: "90%", marginBottom: "14px" }}
                    type={values.showPassword ? "text" : "password"}
                    value={values.password}
                    onChange={handleChange("password")}
                    label="Password"
                />
                 <div className="bt-home">   
                    <Button sx={{backgroundColor:'transparent', width:'70%', color: '#0dcaf0',p:2, mt:'20%'}} color="primary" variant="filled" onClick={login}>
                     L O G I N
                    </Button>
                    <Typography variant="subtitle2" >
                       Olvidaste la contrasea?
                    </Typography>
                </div>
            </div>
        </Box>
    );
}
