import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import RemoveSharpIcon from '@mui/icons-material/RemoveSharp';
import AddSharpIcon from '@mui/icons-material/AddSharp';
import TextField from '@mui/material/TextField';
import InputAdornment from '@mui/material/InputAdornment';
import { useDispatch } from 'react-redux'

import * as Actions from "../../redux/action";
import React, { useEffect, useState } from 'react'
 
 export default function Slide(props) {
      const dispatch = useDispatch();
      const [width, setWidth] =useState(0);
      const [amount,setAmount] = useState('0');
      const [placeholder, setPlaceholder] = useState(0);
      const [count, setCount] = useState(1);
      const [description, setDescription] = useState("");

      const changeHandler = (e) =>{ 
            e.preventDefault();
            const rgx = /^[0-9]*(\.\d{0,2})?$/;
            const result = e.target.value.toString().match(rgx);
            
            if(result && result[0] <= 99999.99){
                setAmount(prev => result ? e.target.value : prev);

                if(e.target.value.slice(0,1)=== '0'){
                    if(e.target.value.slice(0,2) === '0.')
                    {
                    }else{
                        let temp =e.target.value.slice(1);
                        setAmount(temp);
                    }
                }
            }
            
      }
     useEffect(() => {
        if(amount ===''){
            setAmount('0')
        }
        if(amount.slice(0).charCodeAt(0) === 46){
            setAmount(prev => Number(prev).toFixed(2))
        }
        setWidth(4    +amount.toString().length*1.3);
        setPlaceholder(amount);
        if(amount.toString().includes('.')){
            setPlaceholder(prev => Number(prev).toFixed(2))
        }
     },[amount])

     const increase = ()=>{
        setCount(prev => prev+1);
    }     
    const decrease = ()=>{
        setCount(prev => {
            if(prev >=2){
                return prev-1;
            }
            else{
                return prev
            }
        });
        
    }
    const index = props.index;
    useEffect(()=>{

        const ss = {index, count, amount, description};
        dispatch(Actions.slideData(ss));

        return ()=>{
            const ss = {index : 0, count : 1, amount:'', description: ''}
            dispatch(Actions.slideData(ss))
        }
    },[amount, count, dispatch, index, description]);

    
   return (
     
<Grid container direction="column" justifyContent=" space-between" alignItems="center" spacing={1}>
            <Grid item>
                <IconButton aria-label="minus" sx={{mx: 3}} onClick={decrease}>
                    <RemoveSharpIcon color="primary" />
                </IconButton>
                <span className="count">{count}</span>
                <IconButton aria-label="plus" sx={{mx: 3}} onClick={increase}>
                    <AddSharpIcon color="primary" />
                </IconButton>
            </Grid>
            {/* <Grid item className="compose" placeholder = {amount? placeholder : 0}>
                <TextField
                    variant="standard"
                    id="filled-start-adornment" 
                    sx={{ m: 1,pl:0, width: width+ "em", maxWidth : '100%'}}
                    InputProps={{
                        startAdornment: <InputAdornment position="start"><p className='adorment'>S/</p></InputAdornment>,
                        disableUnderline: true,
                        style: { fontSize: 58 }
                    }}
                    type="number"
                    onChange={changeHandler}
                    value={amount}
                />
            </Grid> */}
            <Grid item sx={{
                width:"90%"
            }}>
                <TextField 
                    placeholder="Description"
                    variant="standard"
                    InputProps={{
                        inputProps: {
                            style: { textAlign: "center" },
                        }
                    }}
                    onChange = {(e)=> setDescription(e.target.value)}
                    fullWidth
                    focused
                />
            </Grid>
        </Grid>
   )
 }
 
