import * as types from "./types";

export const saveEmail = (val) => {
    return {
        type: types.SAVE_EMAIL,
        payload: val,
    };
};

export const selData = (val) => {
    return {
        type: types.SEL_DATA,
        payload: val,
    };
};

export const slideData = (val) =>{
    return {
        type: types.SLIDE_DATA,
        payload : val
    }
}