import React, { useState } from "react";
import InputAdornment from "@mui/material/InputAdornment";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import Axios from "axios";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
/////////////////////////////////
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Input from '@mui/material/Input';
import ClearIcon from '@mui/icons-material/Clear';

import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';


export default function Create() {
    const [name, setName] = useState("");
    const [ruc, setRuc] = useState("");
    const [branch, setBranch] = useState("");
    const [token, setToken] = useState("");
    const [email, setEmail] = useState("");
    const [credit, setCredit] = useState("");
    const [vendor, setVendor] = useState("");
    const [process, setProcess] = useState("prueba");
    const [paid, setPaid] = useState("");
    const [values, setValues] = React.useState({
        password: "",
        showPassword: false,
    });
    const [rePassword, setRePassword] = React.useState({
        repassword: "",
        showRePassword: false,
    });
    const [docType, setDocType] = useState({
      invoice: true,
      ticket: true,
      note : true
    })
    const docHandle = (event) =>{
      setDocType({
        ...docType,
        [event.target.name] : event.target.checked
      })
    }
    const { addToast } = useToasts();
    const history = useHistory();

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    const handleClickShowPassword = () => {
        setValues({
            ...values,
            showPassword: !values.showPassword,
        });
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };
    // RePassword
    const handleRePasswordChange = (prop) => (event) => {
        setRePassword({ ...rePassword, [prop]: event.target.value });
    };

    const handleClickShowRePassword = () => {
        setRePassword({
            ...rePassword,
            showRePassword: !rePassword.showRePassword,
        });
    };

    // Submit New User Information to database
    const submit = async () => {
        const password = values.password;
        const repassword = rePassword.repassword;
        if (!name) {
            addToast("Please Enter Name", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!ruc) {
            addToast("Please Enter Ruc", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!branch) {
            addToast("Please Enter Id Sucursal", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!token) {
            addToast("Please Enter Token", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!email) {
            addToast("Please Enter Email", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!vendor) {
            addToast("Please Enter Id Vendor", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!process) {
            addToast("Please Enter Tipo Proceso", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!credit) {
            addToast("Please Enter Credit", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!paid) {
            addToast("Please Enter Paid", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (password !== repassword) {
            addToast("Please Confirm Password", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if(!docType.invoice && !docType.ticket && !docType.note){
          addToast('Please Check Doc Types' , {
            appearance: "error",
            autoDismiss : true
          })
          return;
        }
        const newUser = {
            name,
            ruc,
            branch,
            token,
            email,
            vendor,
            process,
            credit,
            paid,
            password,
            docType
        };
        const resultRegister = await Axios.post("/users/register", newUser);

        // Register Success Case
        if (resultRegister.data.status === 200) {
            history.push("/admin");
        } else {
            addToast(resultRegister.data.msg, {
                appearance: "error",
                autoDismiss: true,
            });
        }
    };
    const prevPage = () => {
      history.push('/admin');
  };
    return (
        <Box
          component="form"
          sx={{ display: 'flex', flexWrap: 'wrap', p:3 }}
          noValidate
          autoComplete="on"
        >
          <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px'}}>
                <ClearIcon />
          </IconButton>
          <div>
          <InputLabel sx={{my:1, color: '#1976d2', fontWeight:'600'}} >New User</InputLabel>
            <TextField
              required
              autoComplete="email"
              variant="standard"
              label="Name"
              focused
              size="small"
              fullWidth
              sx={{mb:1}}
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <TextField
              label="Ruc"
              type="number"
              variant="standard"
              size="small"
              sx={{ mr: 1,my:1, width: '60%' }}
              value={ruc}
              onChange={(e) => setRuc(e.target.value)}
              focused
            />
            <TextField
              label="Id Sucursal"
              variant="standard"
              type="number"
              size="small"
              sx={{ ml: 1,my:1, width: '35%' }}
              value={branch}
              onChange={(e) => setBranch(e.target.value)}
              focused
            />
            <TextField
              label="Token"
              size="small"
              variant="standard"
              focused
              fullWidth
              sx={{my:1}}
              value={token}
              onChange={(e) => setToken(e.target.value)}
            />
            <TextField
              required
              label="Email"
              size="small"
              variant="standard"
              fullWidth
              sx={{my:1}}
              focused
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <TextField
              label="Id Vendor"
              type="number"
              variant="standard"
              size="small"
              sx={{ my: 1,mr:1, width: '40%' }}
              focused
              value={vendor}
              onChange={(e) => setVendor(e.target.value)}
            />
            <FormControl sx={{ my: 1,ml:1, width: '55%' }} size="small" focused variant="standard">
              <InputLabel >Tipo Proceso</InputLabel>
              <Select
                value={process}
                onChange={(e) => setProcess(e.target.value)}
                label="Age"
              >
                <MenuItem value="prueba">Prueba</MenuItem>
                <MenuItem value="other">Other</MenuItem>
              </Select>
            </FormControl>
            <FormControl sx={{my:1}} fullWidth variant="standard" size="small"  focused required>
              <InputLabel >Password</InputLabel>
              <Input

                type={values.showPassword ? 'text' : 'password'}
                value={values.password}
                onChange={handleChange('password')}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                      edge="end"
                    >
                      {values.showPassword ? <VisibilityOff color='info' /> : <Visibility  color='info' />}
                    </IconButton>
                  </InputAdornment>
                }
              />
            </FormControl>
          <FormControl sx={{my:1}} fullWidth variant="standard" size="small" focused>
            <InputLabel>Confirm Password</InputLabel>
            <Input
              type={rePassword.showRePassword ? 'text' : 'password'}
              value={rePassword.repassword}
              onChange={handleRePasswordChange('repassword')}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowRePassword}
                    onMouseDown={handleMouseDownPassword}
                    edge="end"
                    color='info'
                  >
                    {rePassword.showRePassword ? <VisibilityOff color='info' /> : <Visibility color='info' />}
                  </IconButton>
                </InputAdornment>
              }
              label="Password"
            />
          </FormControl>
          <TextField
              label="Id Credit"
              type="number"
              variant="standard"
              sx={{ my: 1,mr:2, width: '45%' }}
              size="small"
              focused
              value={credit}
              onChange={(e) => setCredit(e.target.value)}
            />
            <TextField
              label="Id Paid"
              type="number"
              
              variant="standard"
              sx={{ my: 1,ml:2, width: '45%' }}
              size="small"
              focused
              value={paid}
              onChange={(e) => setPaid(e.target.value)}
            />
            <FormControlLabel  control={<Checkbox  checked={docType.invoice} onChange={docHandle} name="invoice" size="small"/>} label="Invoice" />
            <FormControlLabel control={<Checkbox checked={docType.ticket} onChange={docHandle} name="ticket" size="small"/>} label="Ticket" />
            <FormControlLabel control={<Checkbox checked={docType.note} onChange={docHandle} name="note" size="small"/>} label="Note" />

            <Button onClick={submit} variant="contained" sx={{my:2, width: '65%' }}>
                Save
            </Button>
            
          </div>
      </Box>
    );
}
