import React, { useState, useEffect, useContext } from "react";
import Grid from "@mui/material/Grid";
// import { useSelector } from "react-redux";
import Axios from "axios";
import { useHistory} from "react-router-dom";
import UserContext from "../../context/UserContext";
import "./index.css";
// import Check from '../../images/check.gif'
import Moment from "react-moment";
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
// import printJs from 'print-js'
 import { styled } from '@mui/material/styles';


export default function Confirm(props) {
    const {id} = props.match.params;
    const history = useHistory();
    const { loginStatus, pdf, conf } = useContext(UserContext);
    const [userInfo, setUserInfo] = useState({
        name: "",
        doc: "",
    });
    // let customerId = useSelector((state) => state.email.customerId);

    useEffect(() => {
        if (navigator.share === undefined) {
            if (window.location.protocol === 'http:') {
              window.location.replace(window.location.href.replace(/^http:/, 'https:'));
            } 
          }
        if(pdf.toString() === ''){
            history.push("/translist");
            return;
        }else{
            Axios.get("/customers/get/" + loginStatus.customerId).then(async (response) => {

                setUserInfo({
                    ...userInfo,
                    "name": response.data[0].name,
                    "doc": response.data[0].doc,
                });
            });
        }
        // Get customer information from "customer" table.
       
        return()=>{
            setUserInfo({
                name:"",
                doc:""
            });
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const transList = () => {
        history.push("/translist");
    };
    const currentDate = new Date();
    const curFormatDate = currentDate;
    // const print =async ()=>{
    //     // let url = "https://cors-anywhere.herokuapp.com/"+pdf;
    //     let url  = `https://api.allorigins.win/raw?url=${encodeURIComponent(pdf)}`
    //  printJs({printable: url, type:'pdf', showModal:true})
    // }
    const print = ()=>{
        // let url  = `https://api.allorigins.win/raw?url=${encodeURIComponent(pdf)}`
        sendUrlToPrint(pdf);
    }
    const sendUrlToPrint = (url)=>{
        var  beforeUrl = 'intent:';
        var  afterUrl = '#Intent;';
        // Intent call with component
        afterUrl += 'component=ru.a402d.rawbtprinter.activity.PrintDownloadActivity;'
        afterUrl += 'package=ru.a402d.rawbtprinter;end;';
        document.location=beforeUrl+encodeURI(url)+afterUrl;
        return false;
    }

    
    const share = async () => {
        // let url = "https://cors-anywhere.herokuapp.com/"+pdf;
        let url  = `https://api.allorigins.win/raw?url=${encodeURIComponent(pdf)}`
      
        let blob = await fetch(url).then(r => r.blob());
            // here image is url/location of image
            const file = new File([blob], `${userInfo["name"]? userInfo["name"] : 'Invited'}.pdf`, {type: blob.type});
            if(navigator.share) {
                await navigator.share({
                title: "Share",
                text: "Hello sir.",
                url: window.location.href,
                files: [file]
                })
                .then(() => console.log('Successful share'))
                .catch((error) => console.log('Error in sharing', error));
            }else {
                window.alert(`system does not support sharing files.`);
            }
    }
      const Img = styled('img')({
        display:'block',
        width: '55%',
        maxWidth: '200px',
        height: '50%',
      });
    return (
        <div className="confirm">
            <Grid
                container
                justifyContent="center"
                alignItems="center"
                direction="column"
            >
                <Img src="/check.gif" alt="CheckImage" sx={{my:'auto'}}/>
                
               {/* <img src={Check} alt="checkImage" className="check" /> */}
                <p className="username">
                    
                    {userInfo["name"] ? userInfo["name"] : <>Invited</>}
                </p>
                <p className="p-amount"><sup><sub>S/</sub></sup>{id}</p>
                <Moment format="Do MMMM YYYY - HH:mm">
                                    {curFormatDate}
                        </Moment>
               
            </Grid>
            <Grid
                container
                justifyContent="space-between"
                alignItems="center"
                direction="row"
                sx={{ padding: "50px 40px" }}
            >
                <IconButton aria-label="home" onClick={transList}>
                    <i  className="fas fa-home"></i >
                </IconButton>
                <IconButton aria-label="share" onClick={share}>
                    <i className="fas fa-share-alt"></i>
                </IconButton>
                <IconButton aria-label="print" onClick={print}>
                    <i className="fas fa-print"></i>
                </IconButton>
                
                
            </Grid>
            <Button onClick={()=> {
                if(conf)
                    history.push('/customer')
                else
                    history.push(`/${userInfo["name"] ? 'invoice' : 'invite'}`)
                }} sx={{mt: 4}} variant="contained" >Send Other</Button>
        </div>
    );
}
