import * as React from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from "@mui/material/Typography";
import logoImg from "../../images/fino.png";
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { useHistory } from 'react-router-dom'

export default function BoxSx() {
  const history = useHistory();

  return (
    
    <Box
      sx={{
        height: '100vh',
        backgroundColor: '#1a1938',
      }}
    >
      <Grid
          container
          direction="column"
          justifyContent="center"
          alignItems="center"
          sx={{
            px:3,
            pt: 10
          }}
      >
        <Grid item>
          <img src={logoImg} alt="logo" width="130px" />
        </Grid>
        <Grid item sx={{mt:2}}>
          <Typography variant="h4" sx={{textAlign: 'center', color: '#fff'}}>
            Bienvenido a Fino
          </Typography>
        </Grid>
        <Grid item>
          <Typography variant="subtitle1" style={{textAlign: 'center', color: "#ffffff6e" }}>
            Emite tus facturas y boletas de una manera fcil, rpida y segura usando t telfono. 
          </Typography>
        </Grid>
        <Grid item sx={{ mt: 1, width: '100%'}}>
          <ButtonGroup
              orientation="vertical"
              aria-label="vertical contained button group"
              sx={{width:'80%'}}
          >
            <Button sx={{backgroundColor:'transparent', color: '#0dcaf0',p:2}} color="primary" variant="contained"   onClick={()=> history.push('/register')}>R e g i s t e r</Button>
            <Button sx={{backgroundColor:'transparent', color: '#0dcaf0',p:2}} color="primary" variant="contained"  onClick={()=> history.push('/login')}>L o g i n</Button>
          </ButtonGroup>
        </Grid>
      </Grid>
    </Box>
  );
}