import React, { useState, useContext } from "react";
import { styled } from "@mui/material/styles";
import InputBase from "@mui/material/InputBase";
// import InputAdornment from "@mui/material/InputAdornment";
// import OutlinedInput from "@mui/material/OutlinedInput";
// import InputLabel from "@mui/material/InputLabel";
// import FormControl from "@mui/material/FormControl";
// import Visibility from "@mui/icons-material/Visibility";
// import VisibilityOff from "@mui/icons-material/VisibilityOff";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import Axios from "axios";
import UserContext from "../../context/UserContext";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import * as Actions from "../../redux/action";
import "./index.css";

import ClearIcon from '@mui/icons-material/Clear';

import Box from '@mui/material/Box';


const BootstrapInput = styled(InputBase)(({ theme }) => ({
    "label + &": {
        marginTop: theme.spacing(3),
    },
    "& .MuiInputBase-input": {
        position: "relative",
        borderRadius:'10px',
        backgroundColor: "white",
        border: "1px solid #D9D9D6",
        padding: "12px 16px",
        transition: theme.transitions.create([
            "border-color",
            "background-color",
            "box-shadow",
            "border-width",
        ]),
    },
}));

export default function Register() {
    const dispatch = useDispatch();
    const [email, setEmail] = useState("");
    const [values, setValues] = React.useState({
        password: "",
        showPassword: false,
    });
    const [rePassword, setRePassword] = React.useState({
        repassword: "",
        showRePassword: false,
    });
    const { addToast } = useToasts();
    const { setLoginStatus } = useContext(UserContext);
    const history = useHistory();

    const handleChange = (prop) => (event) => {
        setValues({ ...values, [prop]: event.target.value });
    };

    // const handleClickShowPassword = () => {
    //     setValues({
    //         ...values,
    //         showPassword: !values.showPassword,
    //     });
    // };

    // const handleMouseDownPassword = (event) => {
    //     event.preventDefault();
    // };
    // RePassword
    const handleRePasswordChange = (prop) => (event) => {
        setRePassword({ ...rePassword, [prop]: event.target.value });
    };

    // const handleClickShowRePassword = () => {
    //     setRePassword({
    //         ...rePassword,
    //         showRePassword: !rePassword.showRePassword,
    //     });
    // };

    // Submit New User Information to database
    const submit = async () => {
        const password = values.password;
        const repassword = rePassword.repassword;

        if (!email) {
            addToast("Please Enter Email", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }

        if (password !== repassword) {
            addToast("Please Confirm Password", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }

        const newUser = {
            email,
            password,
        };
        const resultRegister = await Axios.post("/users/register", newUser);

        // Register Success Case
        if (resultRegister.data.status === 200) {
            setLoginStatus(true);

            // Store current user's email to redux
            const payload = {
                email: email,
                customerId: "",
            };
            dispatch(Actions.saveEmail(payload));
            history.push("/login");
        } else {
            addToast(resultRegister.data.msg, {
                appearance: "error",
                autoDismiss: true,
            });
        }
    };
    
    const prevPage = () => {
        history.push('/');
    };
    return (<Box
        sx={{
          height: '100vh',
          backgroundColor: '#1a1938',
        }}
      >
          <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px'}}>
                <ClearIcon />
            </IconButton>
            <div className="top-bar"></div>
            
            <div className="reg-item">
                
            <p className="title">Sign up</p>
                <BootstrapInput
                    placeholder="Email"
                    type="email"
                    sx={{ width: "90%", my: "14px", mt:'25px' }}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
               
                <BootstrapInput
                    placeholder="Password"
                    sx={{ width: "90%", marginBottom: "14px" }}
                    type={values.showPassword ? "text" : "password"}
                    value={values.password}
                    onChange={handleChange("password")}
                    label="Password"
                />
                <BootstrapInput
                    placeholder="Confirm Password"
                    sx={{ width: "90%", marginBottom: "14px" }}
                    type={rePassword.showRePassword ? "text" : "password"}
                    value={rePassword.repassword}
                    onChange={handleRePasswordChange("repassword")}
                    label="Password"
                />
                <div className="bt-home" style={{marginTop:'40px'}}>   
                <Button sx={{backgroundColor:'transparent', color: '#0dcaf0',p:3,width:'70%'}} color="primary" variant="filled" onClick={submit}>
                    R e g i s t e r
                </Button>

                </div>
            </div>
        </Box>
    );
}
