import React, { useEffect, useState, useContext } from "react";
import UserContext from "../../context/UserContext";
import { useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import Axios from "axios";
import "./index.css";
import Slide from '../Invoice/Slide'
import { useToasts } from "react-toast-notifications";
//mui component
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import ClearIcon from '@mui/icons-material/Clear';
import IconButton from '@mui/material/IconButton';
// import Button from '@mui/material/Button';
import SendApi from '../api/sendApi';

import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react';

import LoadingButton from '@mui/lab/LoadingButton';
import SendIcon from '@mui/icons-material/Send';
// Import Swiper styles
import 'swiper/swiper.min.css'


export default function Invite() {
    const { setLoginStatus, setPdf , setConf} = useContext(UserContext);
    const [invoiceInfo, setInvoiceInfo] = useState({
        amount: "",
        name: "Invited",
        doc: "",
        direction: "",
        sellerId: "",
        customerId: "",
        type: "",
        pdfLink:""
    });
    const [loading, setLoading] = React.useState(false);
    const { addToast } = useToasts();
    const history = useHistory();
    // let email = useSelector((state) => state.email.currentEmail);
    // let email = loginStatus.email;
    const [docType, setDocType] = useState({
        invoice: false,
        ticket: false,
        note: false
  
      });
    useEffect(() => {
        const checkLoggedIn = async () => {
            let token = localStorage.getItem("auth-token");
            if (!token) {
              history.push("/");
              return;
            }
              const userRes = await Axios.get("/users/", {
                headers: { "x-auth-token": token },
              });
              setLoginStatus({
                token,
                email: userRes.data.email,
                customerId:''
              });
            
    
              Axios.get("/users/getId/" + userRes.data.email).then(
                async (response) => {
                  setDocType({...docType, 'invoice' : response.data.docType[0], 'ticket' : response.data.docType[1], 'note' : response.data.docType[2]})
                  setInvoiceInfo({
                      ...invoiceInfo,
                      "sellerId" : response.data._id
                  })
                if(response.data.docType[1] && !response.data.docType[2]){
                    setInvoiceInfo({
                        ...invoiceInfo,
                        "type" : 'ticket',
                        "sellerId": response.data._id,
                    })
                    
                    setDocType({...docType, 'ticket' : false});
                }
        
                if(!response.data.docType[1] && response.data.docType[2]){
                    setInvoiceInfo({
                        ...invoiceInfo,
                        "type" : 'note',
                        "sellerId": response.data._id,
                    })
                    setDocType({...docType, 'note' : false});
                }
                })

          };
          checkLoggedIn();    
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const prevPage = () => {
        history.push('/customer');
    };
    
    const [slideCount, setSlideCount] = useState(2); 

    const increment = ()=>{
        setSlideCount(prev=> prev +1)
    }
    
    //get total
    const dataInfo = useSelector(state => state.slide)
    const [ middle, setMiddle] = useState([]);
    
    const collection = [];
    
    const sort = () =>{
        for(let i = 0; i<slideCount-1 ; i++){
             collection.push(middle.filter((el)=> {
                return el.index === i ;
            }).pop());
        }
    }
    const [total, setTotal]=useState(0);
    const getTotal=()=>{
        let sum = 0;
        for(let i = 0; i< collection.length; i++){
            if(collection[i] === undefined){

            }else{
                sum += collection[i].count * Number(collection[i].amount);
            }
        }
        setTotal(sum.toFixed(2))
    }
    useEffect(()=>{
        setMiddle(prev=> [...prev, dataInfo])
    },[dataInfo])
    useEffect(()=>{
        sort();
        getTotal()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [middle])
    useEffect(()=>{
        setInvoiceInfo({
            ...invoiceInfo,
            "amount" : total.toString()
        });
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[total])
    const submit = (e) => {
        if (!invoiceInfo["amount"]) {
            addToast("Please Enter Amount", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            return;
        }
        // v9alidation()
        sort()
        if(collection[0] === undefined){
            addToast("Please Enter Description", {
                appearance: "info",
                autoDismissTimeout: 2000,
                autoDismiss: true,
                placement: 'top-center'
            });
            return;
        }
        for(let i = 0; i< collection.length; i++){
            if(collection[i].count === 1 && collection[i].amount === '0'){

            }else{
                if(collection[i].description === ''){
                    addToast("Please Enter Description", {
                        appearance: "info",
                        autoDismiss: true,
                        autoDismissTimeout: 2000,
                        placement: 'top-center'
                    });
                    return;
                 }
                 if(collection[i].amount === '0'){
                    addToast("Please Enter amount", {
                        appearance: "info",
                        autoDismiss: true,
                        autoDismissTimeout: 2000,
                        placement: 'top-center'
                    });
                    return;
                 }
            }
        }
        if (!invoiceInfo["type"]) {
            addToast("Please Select Type", {
                appearance: "info",
                autoDismiss: true,
                autoDismissTimeout: 2000,
                placement: 'top-center'
            });
            return;
        }
        setLoading(true);
        setPdf('http://example.com');
        Axios.get("/users/get/"+ invoiceInfo.sellerId).then((response)=>{
            const postDetail = SendApi(collection, invoiceInfo, response.data);

            getVals(postDetail, invoiceInfo.type);
        })
    };

    async function getVals(detail, type){
        
        return await fetch(type === 'note' ? "https://facturavirtual.pe/facturacion/api/procesar_nota_venta": "https://facturavirtual.pe/facturacion/api/procesar_venta", detail)
        .then((response)=>{
                 return response.json()
        })

        .then((datas) => { 
            //   const gettingPdf = `https://facturavirtual.pe/sys_fe/download/downloadpdf/${datas.documento.id_contribuyente}/${datas.documento.id_tipodoc_electronico}/${datas.documento.serie_comprobante}/${datas.documento.numero_comprobante}/pdf/ticket`;
            const gettingPdf =datas.url_absoluta_ticket;
              setPdf(gettingPdf.toString());
              setInvoiceInfo({
                  ...invoiceInfo,
                  pdfLink : gettingPdf
              })
              let tmp_invoice = {...invoiceInfo,"pdfLink" : gettingPdf.toString()};
              
              Axios.post("/trans/create", tmp_invoice).then(async (response) => {
                if (response.data.status === 200) {
                    setConf(true)
                    history.push({
                        pathname: "/confirm/" + invoiceInfo["amount"],
                    });
                }
            });
              //return gettingPdf;
        })
        .catch((err)=>{ 
            addToast("En el ambiente de pruebas solo se permiten crear hasta 10 BOLETAS", {
            appearance: "info",
            autoDismissTimeout: 2000,
            autoDismiss: true,
            placement: 'top-center'
        });
        setLoading(false);
        return;})
    }


    useEffect(()=>{
        return()=>{
            setTotal();
            collection.fill(null);
            setMiddle(null);
            setSlideCount(2);
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    return (
        <Box sx={{ 
            flexGrow: 1,
            width: '100%',
            height: '100%',
            backgroundColor : 'white'
        }}>
            
            <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px'}}>
                <ClearIcon />
            </IconButton>

    <Grid container direction="column" sx={{mt:8}} justifyContent="space-between" alignItems="center" >
        <Grid item>
            <p className="doc">Invited</p>
        </Grid>
        <Grid sx={{width: '100%', mt:1}} item>
            <Swiper
                spaceBetween={0}
                slidesPerView={1}
                onReachEnd = {()=>increment()}
            >

                {
                    (new Array(slideCount).fill(null).map((_, i)=>(
                        <SwiperSlide  key={i}><Slide index={i}/></SwiperSlide>
                    )))
                }

            </Swiper>
        </Grid>

        <Grid item sx={{
            mt:2
        }}>
            <RadioGroup
                row
                aria-labelledby="demo-row-radio-buttons-group-label"
                name="row-radio-buttons-group"
                sx={{
                    pl:2
                }}
            >
                
                {docType.ticket && <FormControlLabel name="type" value="ticket" control={<Radio size="small" />} label="Ticket" onChange={(e) =>
                            setInvoiceInfo({
                                ...invoiceInfo,
                                [e.target.name]: e.target.value,
                            })
                        }/>}
                {docType.note && <FormControlLabel name="type" value="note" control={<Radio size="small" />} label="Note" onChange={(e) =>
                    setInvoiceInfo({
                        ...invoiceInfo,
                         [e.target.name]: e.target.value,
                    })
                        }/>}        
            </RadioGroup>
        </Grid>

        <Grid item sx={{
            mt:4
        }}>
            <p className="total">Total S/{total}</p>
        </Grid>
        <Grid item sx={{
            width: '60%',
            mt:1
        }}>
            <LoadingButton
          onClick={submit}
          endIcon={<SendIcon />}
          loading={loading}
          loadingPosition="end"
          fullWidth
          variant="contained"
        >
          { invoiceInfo.type === '' ? 'Send' : invoiceInfo.type.toUpperCase() }
        </LoadingButton> 
        </Grid>
    </Grid>
    </Box>

    );
}
