import { useParams } from "react-router-dom";
import React, { useState, useEffect } from "react";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import Axios from "axios";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";

/////////////////////////////////
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import ClearIcon from '@mui/icons-material/Clear';

import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';



export default function Edit() {
  let { id } = useParams();
    const [name, setName] = useState("");
    const [ruc, setRuc] = useState("");
    const [branch, setBranch] = useState("");
    const [token, setToken] = useState("");
    const [email, setEmail] = useState("");
    const [credit, setCredit] = useState("");
    const [vendor, setVendor] = useState("");
    const [process, setProcess] = useState("");
    const [paid, setPaid] = useState("");
    const [docType, setDocType] = useState({
      invoice: false,
      ticket: false,
      note: false

    });
    const docHandle = (event) =>{
      setDocType({
        ...docType,
        [event.target.name] : event.target.checked
      })
    }


    const { addToast } = useToasts();
    const history = useHistory();
 
  const submit = async () => {
        if (!name) {
            addToast("Please Enter Name", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!ruc) {
            addToast("Please Enter Ruc", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!branch) {
            addToast("Please Enter Id Sucursal", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!token) {
            addToast("Please Enter Token", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!email) {
            addToast("Please Enter Email", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!vendor) {
            addToast("Please Enter Id Vendor", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!process) {
            addToast("Please Enter Tipo Proceso", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!credit) {
            addToast("Please Enter Credit", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if (!paid) {
            addToast("Please Enter Paid", {
                appearance: "error",
                autoDismiss: true,
            });
            return;
        }
        if(!docType.invoice && !docType.ticket && !docType.note){
          addToast('Please Check Doc Types' , {
            appearance: "error",
            autoDismiss : true
          })
          return;
        }

    const newUser = {
      id,
      name,
      ruc,
      branch,
      token,
      email,
      vendor,
      process,
      credit,
      paid,
      docType
    };
    const resultRegister = await Axios.put(
      "/users/update",
      newUser
    );

    // Register Success Case
    if (resultRegister.status === 200) {
      history.push("/admin");
    } else {
      addToast(resultRegister.data.msg, {
        appearance: "error",
        autoDismiss: true,
      });
    }
  };
    const prevPage = () => {
      history.push('/admin');
  };
  
    useEffect(() => {
    Axios.get("/users/get/" + id).then(
      async (response) => {
        setName(response.data.name);
        setRuc(response.data.ruc);
        setBranch(response.data.branch);
        setToken(response.data.token);
        setCredit(response.data.credit);
        setVendor(response.data.vendor);
        setProcess(response.data.process? response.data.process : "prueba");
        setPaid(response.data.paid);
        setEmail(response.data.email);
        setDocType({...docType, 'invoice' : response.data.docType[0], 'ticket' : response.data.docType[1], 'note' : response.data.docType[2]})
      }
    );
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [ id]);

    return (
        <Box
          component="form"
          sx={{ display: 'flex', flexWrap: 'wrap', p:3 }}
          noValidate
          autoComplete="on"
        >
          <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px'}}>
                <ClearIcon />
          </IconButton>
          <div>
          <InputLabel sx={{my:1, color: '#1976d2', fontWeight:'600'}} >Edit User</InputLabel>
            <TextField
              required
              autoComplete="email"
              variant="standard"
              label="Name"
              focused
              size="small"
              fullWidth
              sx={{mb:1}}
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
            <TextField
              label="Ruc"
              type="number"
              variant="standard"
              size="small"
              sx={{ mr: 1,my:1, width: '60%' }}
              value={ruc}
              onChange={(e) => setRuc(e.target.value)}
              focused
            />
            <TextField
              label="Id Sucursal"
              variant="standard"
              type="number"
              size="small"
              sx={{ ml: 1,my:1, width: '35%' }}
              value={branch}
              onChange={(e) => setBranch(e.target.value)}
              focused
            />
            <TextField
              label="Token"
              size="small"
              variant="standard"
              focused
              fullWidth
              sx={{my:1}}
              value={token}
              onChange={(e) => setToken(e.target.value)}
            />
            <TextField
              required
              label="Email"
              size="small"
              variant="standard"
              fullWidth
              sx={{my:1}}
              focused
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <TextField
              label="Id Vendor"
              type="number"
              variant="standard"
              size="small"
              sx={{ my: 1,mr:1, width: '40%' }}
              focused
              value={vendor}
              onChange={(e) => setVendor(e.target.value)}
            />
            <FormControl sx={{ my: 1,ml:1, width: '55%' }} size="small" focused variant="standard">
              <InputLabel >Tipo Proceso</InputLabel>
              <Select
                value={process}
                onChange={(e) => setProcess(e.target.value)}
                label="Age"
              >
                <MenuItem value="prueba">Prueba</MenuItem>
                <MenuItem value="other">Other</MenuItem>
              </Select>
            </FormControl>
          <TextField
              label="Id Credit"
              type="number"
              variant="standard"
              sx={{ my: 1,mr:2, width: '45%' }}
              size="small"
              focused
              value={credit}
              onChange={(e) => setCredit(e.target.value)}
            />
            <TextField
              label="Id Paid"
              type="number"
              
              variant="standard"
              sx={{ my: 1,ml:2, width: '45%' }}
              size="small"
              focused
              value={paid}
              onChange={(e) => setPaid(e.target.value)}
            />

            <FormControlLabel  control={<Checkbox  checked={docType.invoice} onChange={docHandle} name="invoice" size="small"/>} label="Invoice" />
            <FormControlLabel control={<Checkbox checked={docType.ticket} onChange={docHandle} name="ticket" size="small"/>} label="Ticket" />
            <FormControlLabel control={<Checkbox checked={docType.note} onChange={docHandle} name="note" size="small"/>} label="Note" />

            <Button onClick={submit} variant="contained" sx={{my:2, width: '65%' }}>
                Save
            </Button>
          </div>
      </Box>
    );
}
