import React, { useState, useEffect} from "react";
import Axios from "axios";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
// import UserContext from "../../context/UserContext";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';



import "./index.css";

export default function Admin() {
    const [users, setUsers] = useState([]);
    const { addToast } = useToasts();
    const history = useHistory();
    useEffect(() => {

        Axios.get("/users/all").then((response) => {
            var users = [];
            for (let i = 0; i < response.data.length; i++) {
                var user = [];
                user = {
                    id: response.data[i].id,
                    name: response.data[i].name,
                    ruc: response.data[i].ruc,
                    branch: response.data[i].branch,
                    vendor: response.data[i].vendor,
                    process: response.data[i].process,
                    credit: response.data[i].credit,
                    paid: response.data[i].paid,
                    token: response.data[i].token,
                    email: response.data[i].email,
                    active_status: response.data[i].active_status,
                };
                users.push(user);
            }
            setUsers(users);

        });
        
        return()=>{
            setUsers([]);
        }
    }, [history]);

    const logout = () => {
        history.push('/')
    };


    const [open, setOpen] = React.useState('');

    const handleClickOpen = (id) => {
        setOpen(id);
    };

    const handleClose = () => {
        setOpen('');
    };


    const remove = (id) => {
        //Before clicking "Delete" button,
    
        var prevUsers = users;

        Axios.delete("/users/delete/" + id).then((response) => {
            if (response.data.msg === "success") {
                var del_id = response.data.del_id;
                var userSize = Object.keys(prevUsers).length;
                var users = [];
                for (let i = 0; i < userSize; i++) {
                    var user = [];
                    if (del_id !== prevUsers[i].id) {
                        user = {
                            id: prevUsers[i].id,
                            name: prevUsers[i].name,
                            ruc: prevUsers[i].ruc,
                            branch: prevUsers[i].branch,
                            vendor: prevUsers[i].vendor,
                            process: prevUsers[i].process,
                            credit: prevUsers[i].credit,
                            paid: prevUsers[i].paid,
                            token: prevUsers[i].token,
                            email: prevUsers[i].email,
                            active_status: prevUsers[i].active_status,
                        };
                        users.push(user);
                    }
                }
                setUsers(users);
                addToast("Deleted Successfully", {
                    appearance: "success",
                    autoDismiss: true,
                });
            } else {
                addToast("Sorry, Operation is failure", {
                    appearance: "error",
                    autoDismiss: true,
                });
            }
        });
        handleClose();
    };

    const deactive = (id) => {
        Axios.get("/users/deactive/" + id).then((response) => {
            var users = [];
            for (let i = 0; i < response.data.length; i++) {
                var user = [];
                user = {
                    id: response.data[i].id,
                    name: response.data[i].name,
                    ruc: response.data[i].ruc,
                    branch: response.data[i].branch,
                    vendor: response.data[i].vendor,
                    process: response.data[i].process,
                    credit: response.data[i].credit,
                    paid: response.data[i].paid,
                    token: response.data[i].token,
                    email: response.data[i].email,
                    active_status: response.data[i].active_status,
                };
                users.push(user);
            }
            setUsers(users);
            addToast("Success, Deactivation", {
                appearance: "success",
                autoDismiss: true,
            });
        });
    };

    const active = (id) => {
        Axios.get("/users/active/" + id).then((response) => {
            var users = [];
            for (let i = 0; i < response.data.length; i++) {
                var user = [];
                user = {
                    id: response.data[i].id,
                    name: response.data[i].name,
                    ruc: response.data[i].ruc,
                    branch: response.data[i].branch,
                    vendor: response.data[i].vendor,
                    process: response.data[i].process,
                    credit: response.data[i].credit,
                    paid: response.data[i].paid,
                    token: response.data[i].token,
                    email: response.data[i].email,
                    active_status: response.data[i].active_status,
                };
                users.push(user);
            }
            setUsers(users);
            addToast("Success, Activation", {
                appearance: "success",
                autoDismiss: true,
            });
        });
    };

    const edit = (id) => {
        history.push({
            pathname: "/admin/edit/" + id,
        });
    };

    const newUser = () => {
        history.push("/admin/new");
    };

    return (
        <div className="admin">
            <div>
                <IconButton onClick={logout}><i
                    className="fas fa-sign-out-alt fa-rotate-180"
                    
                ></i>
                </IconButton>
            </div>
             <i className="fas fa-user-plus" onClick={newUser}></i>
            <table className="table table-hover">
                <thead>
                    <tr>
                        <th>No</th>
                        <th>Ruc</th>
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {users.map((user, index) => (
                        <tr key={index}>
                            <td>{index + 1}</td>
                            <td>{user.ruc}</td>
                            <td>
                                
                                <IconButton size="small" onClick={() => edit(user.id)}><i
                                    className="fas fa-pen"
                                ></i>
                                </IconButton>
                                <IconButton size="small" onClick={() => handleClickOpen(user.id)}>
                                <i
                                    className="fas fa-trash-alt"
                                >
                                </i></IconButton>
                               
                                {user.active_status ? (
                                     <IconButton size="small" onClick={() => deactive(user.id)}>
                                    <i
                                        className="fas fa-lock"
                                    ></i></IconButton>
                                ) : (
                                    <IconButton size="small" onClick={() => active(user.id)}><i
                                        className="fas fa-lock-open"
                                    ></i></IconButton>
                                    
                                )}
                                
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
            <Dialog
                                        open={open ? true : false}
                                        onClose={handleClose}
                                        aria-labelledby="alert-dialog-title"
                                        aria-describedby="alert-dialog-description"
                                    >
                                        <DialogTitle id="alert-dialog-title">
                                        {"Caution"}
                                        </DialogTitle>
                                        <DialogContent>
                                        <DialogContentText id="alert-dialog-description">
                                            All data will be deleted.
                                        </DialogContentText>
                                        </DialogContent>
                                        <DialogActions>
                                        <Button onClick={handleClose}>{"Cancel"}</Button>
                                        <Button onClick={() => remove(open)} autoFocus>
                                            {"Okay"}
                                        </Button>
                                        </DialogActions>
                                    </Dialog>
        </div>
    );
}
